@import url(https://fonts.googleapis.com/css2?family=M+PLUS+Rounded+1c:wght@300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500&display=swap);
.nav {
  position: fixed;
  width: 100%;
  color: white;
  background: linear-gradient(
    rgba(0, 0, 0, 0.65) 10%,
    rgb(0, 0, 0, 0.3) 70%,
    rgba(255, 255, 255, 0) 100%
  );
  transition: all 700ms ease-out;
  z-index: 20;
}
.nav-link{
  display: flex;
  text-decoration: none;
}
#browse {
  display: none;
}
.black {
  background: black;
  transition: 500ms ease-in;
}
.nav-container {
  display: flex;
  justify-content: space-between;
  width: 95%;
  margin: 0 auto;
  height: 65px;
}
small {
  font-size: 10px;
}
ul {
  display: flex;
}
li {
  align-self: center;
  list-style: none;
  cursor: pointer;
  font-weight: 400;
  margin: 10px;
  color: white;
}
.first li:not(:first-child) {
  color: rgb(224, 224, 224);
  transition: 200ms ease-in;
}
.first li:hover:not(.active) {
  color: rgb(197, 197, 197);
  opacity: 0.9;
  transition: 100ms ease-in;
}
.second li {
  margin: 0 15px;
}
.logo {
  width: 100px;
  margin: 8px 20px 0 0;
}
.active {
  text-shadow: 0px 0px 1.5px rgb(255, 255, 255);
  transform: scale(1.015);
  color: white !important;
  cursor: default;
  transition: 150ms ease-out;
}
.user {
  margin: 6px 0 0;
  border-radius: 3px;
}
.search {
  position: relative;
}
.fas {
  transform: scale(1.2);
}
#search-icon:active {
  transform: scale(1.4);
  transition: 50ms ease-out;
}
#search-li {
  margin-right: -40px;
}
.blank {
  display: none;
}
@keyframes search {
  from {
    width: 0px;
  }
  to {
    width: 167px;
  }
}
@keyframes search-reverse {
  0% {
    width: 167px;
    opacity: 1;
  }
  98% {
    width: 0px;
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.left {
  transform: translate(-180px, 1.5px);
  transition: 310ms ease-in-out;
}
.right {
  transform: translate(0px, 0px);
  transition: 190ms ease-in;
}
#search-input {
  font-size: 15px;
  height: 22px;
  padding: 5px 5px 5px 38px;
  color: rgb(255, 255, 255);
  background-color: rgba(0, 0, 0, 0.75);
  border: 1px solid white;
  outline: none;
}


@media (max-width: 1000px) {
  .second {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }
  .first {
    flex: 1 1;
  }
  .nav-container {
    height: 55px;
    position: relative;
  }
  .logo {
    width: 80px;
    margin: 7px 5px 0 0;
  }
  .first li:not(:first-child) {
    font-size: 14px;
  }
  .second li:nth-child(3) {
    display: none;
  }
}

@media (max-width: 860px) {
  li {
    font-size: 12px;
  }
  .user {
    width: 25px;
    margin-bottom: 2px;
  }
  .logo {
    width: 60px;
    margin: 5.5px 0 0 0;
  }
  .first li:not(:first-child) {
    font-size: 13px;
  }
  .second li:nth-child(3) {
    display: none;
  }
  #search-icon {
    font-size: 12px;
  }
  small {
    font-size: 8px;
  }
  .nav-container {
    height: 50px;
  }
}
@media (max-width: 755px) {
  .nav-link{
    display: none;
  }
  #browse {
    color: white;
    display: inline-block;
    font-size: 12px;
  }
}

.billboard {
  width: 100%;
  height: 0;
  padding-top: 60%;
  background-size: contain;
  background-position-x: center;
  background-repeat: no-repeat;
  position: relative;
}
.billboard::after {
  content: "";
  position: absolute;
  pointer-events: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  padding-top: 56.4%;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 60%,
    rgba(0, 0, 0, 1)
  );
}
.billboard::before {
  content: "";
  position: absolute;
  pointer-events: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  padding-top: 56.5%;
  background: linear-gradient(
    to left,
    rgba(0, 0, 0, 0) 30%,
    rgba(0, 0, 0, 0.6) 60%,
    rgba(0, 0, 0, 0.8) 95%
  );
}
.movie-desc {
  position: absolute;
  bottom: 35%;
  left: 5%;
  width: 45%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  z-index: 5;
}
.title {
  color: white;
  font-size: 4vw;
}
.desc {
  color: white;
  font-family: "M PLUS Rounded 1c", sans-serif;
  font-weight: 500;
  font-size: 1.35vw;
  margin-bottom: 30px;
  text-shadow: 1px 1px 1px rgb(0, 0, 0);
}
.btn {
  border: none;
  outline: none;
  cursor: pointer;
  font-weight: bold;
  padding: 0.45vw 1.8vw;
  border-radius: 4px;
  font-size: 18px;
}
.play-btn {
  background-color: white;
  margin-right: 10px;
}
.info-btn {
  color: white;
  background-color: rgba(128, 128, 128, 0.8);
}
.play-btn:hover {
  background-color: rgba(255, 255, 255, 0.7);
}
.info-btn:hover {
  background-color: rgba(128, 128, 128, 0.5);
}

@media (max-width: 850px) {
  .btn {
    padding: 0.45vw 1.8vw;
    font-size: 15px;
  }
}
@media (max-width: 480px) {
  .btn {
    padding: 0.4vw 1.7vw;
    font-size: 10px;
  }
}

.movie-details {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  color: white;
  pointer-events: none;
  width: 0;
  height: 0;
  opacity: 0;
}
.blank {
  display: none;
}
.details-third-row{
  padding-bottom: 3px;
}
.details-first-row,
.details-second-row,
.details-third-row {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  width: 96%;
}
.first-column-icons{
  display: flex;
}
.details-name {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  flex: 0.95 1;
}
.details-date {
  font-family: "Inter", sans-serif;
  align-self: flex-start;
  font-size: 8px;
  font-weight: 400;
  padding: 1.2px 2.4px;
  margin-top: 2px;
  border-radius: 1px;
  border: 0.1px solid white;
}
.details-icon {
  position: relative;
  border: 1.6px solid rgb(170, 170, 170);
  width: 27px;
  height: 27px;
  border-radius: 50%;
  transition: 100ms ease-out;
}
.details-icon:hover {
  border-color: white;
  transition: 100ms ease-in;
}
.details-icon:first-child {
  background-color: white;
  border-color: white;
  transition: 100ms ease-out;
}
.details-icon:first-child:hover {
  background-color: rgba(255, 255, 255, 0.85);
  border-color: rgba(255, 255, 255, 0.85);
  transition: 100ms ease-in;
}
.fa-play-icon {
  color: black;
}
.details-icon:not(:last-child) {
  margin-right: 5px;
}
.fa-plus,
.fa-check,
.fa-play-icon,
.fa-thumbs-up,
.fa-thumbs-down,
.fa-chevron-down {
  font-size: 14px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.fa-chevron-down {
  top: 53%;
}
.fa-play-icon {
  top: 54%;
  left: 53%;
}
.fa-thumbs-down {
  left: 52%;
}
.fa-thumbs-up {
  left: 52%;
  top: 47.5%;
}
.play-icon {
  font-size: 17px;
}


.check-icon::before,
.plus-icon::before,
.thumbsUp-icon::before,
.thumbsDown-icon::before,
.info-icon::before {
  position: absolute;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  pointer-events: none;
  color: black;
  background-color: #fff;
  padding: 2.5px 9px;
  top: -40px;
  left: 50%;
  border-radius: 3px;
  font-size: 12px;
  font-weight: 600;
  transform: translateX(-50%);
  box-shadow: 0px 5px 2px rgba(0, 0, 0, 0.4);
  opacity: 0;
}
.check-icon::after,
.plus-icon::after,
.thumbsUp-icon::after,
.thumbsDown-icon::after,
.info-icon::after {
  content: "";
  position: absolute;
  top: -17px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
}
.check-icon::before{
  content: "Remove from My List";
}
.plus-icon::before {
  content: "Add to My List";
}
.thumbsUp-icon::before {
  content: "I like this";
}
.thumbsDown-icon::before {
  content: "Not for me";
}
.info-icon::before {
  content: "More info";
}

.check-icon:hover::before,
.plus-icon:hover::before,
.thumbsUp-icon:hover::before,
.thumbsDown-icon:hover::before,
.info-icon:hover::before {
  opacity: 1;
}
.check-icon:hover::after,
.plus-icon:hover::after,
.thumbsUp-icon:hover::after,
.thumbsDown-icon:hover::after,
.info-icon:hover::after {
  border-top: 8px solid white;
}

.details-genres {
  font-family: "Poppins", sans-serif;
  flex: 0.95 1;
  font-size: 9.5px;
}
.details-match {
  font-family: "Poppins", sans-serif;
  font-size: 9px;
  font-weight: 600;
  color: #3CCA3C;
}

.carousel-container {
  position: relative;
  margin-bottom: 25px;
}
#car {
  height: 140px;
}
#car:hover {
  z-index: 2;
  transition-delay: 600ms;
}
.carousel-title {
  font-size: 2vw;
  color: white;
  margin-left: 5%;
}
.movie-container {
  width: 100%;
  transition: 200ms cubic-bezier(0, 1.1, 0.96, 0.99);
}
.movie-container:hover {
  transform: scale(1.5) translate(-12px, -40px);
  z-index: 10;
  transition: 260ms cubic-bezier(0.35, 0.84, 0.59, 0.93);
  transition-delay: 600ms;
}
.movie-container:first-child:hover {
  transform: scale(1.5) translate(29px, -40px);
}
.movie-container:last-child:hover {
  transform: scale(1.5) translate(-55px, -40px);
}
.movie-container:hover .movie {
  border-radius: 3px 3px 0 0;
  box-shadow: 0px -2px 2px 2px rgba(0, 0, 0, 0.5);
  transition-delay: 600ms;
}
.movie-container::before {
  content: "";
  pointer-events: none;
  position: absolute;
  top: 99.5%;
  height: 100px;
  background: rgb(30, 30, 30);
  width: calc(100% + 73.8px);
  opacity: 0;
  z-index: -5;
}
.movie-container:hover::before {
  cursor: pointer;
  pointer-events: all;
  transition: 200ms;
  transition-delay: 600ms;
  opacity: 1;
  box-shadow: 1px 2px 3px 2px rgba(0, 0, 0, 0.7);
  border-radius: 0 0 3px 3px;
}

.movie-container:hover .movie-details {
  cursor: pointer;
  height: 90px;
  width: calc(100% + 73px);
  opacity: 1;
  pointer-events: all;
  transition-delay: 600ms;
}
.movie {
  background-position: center;
  background-size: cover;
  cursor: pointer;
  height: 140px;
  width: auto;
  border-radius: 3px;
}
.swiper-wrapper {
  transition-timing-function: ease;
}
.swiper-slide:nth-child(odd),
.swiper-slide:nth-child(even) {
  width: 175px;
}
.swiper-slide:first-child {
  margin-left: 5%;
}
.swiper-slide:last-child {
  margin-right: 75px;
}
.swiper-button-next,
.swiper-button-prev {
  color: white !important;
  opacity: 0 !important;
}
.swiper-button-next::before,
.swiper-button-prev::before {
  content: "";
  position: absolute;
  pointer-events: all;
  height: 140px;
  width: 50px;
  opacity: 0;
  z-index: -1;
  transition: 200ms ease-in;
}
.swiper-button-prev::before {
  left: -10;
  background: linear-gradient(to right, rgb(0, 0, 0), rgba(0, 0, 0, 0.4));
}
.swiper-button-next::before {
  right: -10;
  background: linear-gradient(to left, rgb(0, 0, 0), rgba(0, 0, 0, 0.4));
}

#car:hover > .swiper-button-next::before,
#car:hover > .swiper-button-prev::before {
  transition: 400ms ease-out;
  opacity: 1;
}
#car:hover > .swiper-button-prev,
#car:hover > .swiper-button-next {
  transition: 400ms ease-out;
  opacity: 1 !important;
}

.swiper-button-prev.swiper-button-disabled,
.swiper-button-next.swiper-button-disabled {
  display: none !important;
}
.swiper-container {
  overflow: visible !important;
}
@media (max-width: 1000px) {
  .carousel-title {
    font-size: 16px;
  }
  .swiper-button-next,
  .swiper-button-prev {
    display: none !important;
  }
  .movie-carousel::before,
  .movie-carousel::after {
    background: rgba(0, 0, 0, 0);
  }
  .movie-container:hover {
    transform: none;
    z-index: 0;
    transition: none;
    transition-delay: 0;
  }
  .movie-container:first-child:hover,
  .movie-container:last-child:hover {
    transform: none;
  }
  .movie-container:hover .movie {
    border-radius: 3px;
    box-shadow: none;
    transition-delay: 0;
  }
  .movie-container:hover .movie-details,
  .movie-container:hover::before {
    display: none;
  }
  .movie-container {
    margin: 0 0;
  }
  .movie,
  #car {
    height: 14.3vw;
  }
  .swiper-slide:nth-child(odd),
  .swiper-slide:nth-child(even) {
    width: 26vw;
  }
}

.movie-details-overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 30;
  overflow-y: scroll;
}
.movie-details-expanded {
  animation: fadeIn 400ms ease-out;
  margin: 35px auto;
  width: 55%;
  border-radius: 10px;
  background: rgb(20, 20, 20);
  z-index: 40;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.7);
}
.movie-details-close {
  position: absolute;
  text-align: center;
  cursor: pointer;
  color: white;
  background-color: rgb(20, 20, 20);
  font-weight: 300;
  width: 35px;
  height: 35px;
  font-size: 30px;
  border-radius: 50%;
  line-height: 110%;
  top: 15px;
  right: 15px;
}
.movie-details-description {
  padding: 10px 10px;
  color: white;
}
.movie-details-title {
  margin-bottom: 5px;
  font-size: 2vw;
  z-index: 10;
}
.movie-details-overview {
  padding-bottom: 20px;
}
.movie-details-video {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  border: none;
}
.videoWrapper {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
}
.movie-details-genresAndDate {
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
  font-size: 1.2vw;
}
.movie-details-image {
  width: 100%;
  height: 0;
  padding-top: 57%;
  border-radius: 10px 10px 0 0;
  background-size: contain;
  background-position-x: center;
  background-repeat: no-repeat;
  position: relative;
}
.movie-details-image::after {
  content: "";
  position: absolute;
  pointer-events: none;
  top: 0;
  height: 0;
  width: 100%;
  padding-top: 56.4%;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 50%,
    rgba(0, 0, 0, 1)
  );
}

@keyframes fadeIn {
  from {
    transform: translateY(-40px);
    opacity: 0;
  }
  to {
    transform: translateY(0px);
    opacity: 1;
  }
}
@keyframes fadeOut {
  from {
    transform: translateY(0px);
    opacity: 1;
  }
  to {
    transform: translateY(60px);
    opacity: 0;
  }
}
@keyframes overlay {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@media (max-width: 1000px) {
  .movie-details-expanded {
    width: 90%;
  }
  .movie-details-title {
    font-size: 20px;
  }
  .movie-details-genresAndDate {
    font-size: 14px;
  }
}


/* SCROLLBAR */
.movie-details-overlay::-webkit-scrollbar {
  width: 16px;
}
.movie-details-overlay::-webkit-scrollbar-track {
  background-color: rgb(20, 20, 20);
}
.movie-details-overlay::-webkit-scrollbar-thumb {
  background-color: white;
  border-radius: 2px;
}
::-webkit-scrollbar-button:single-button {
  background-color: rgb(20, 20, 20);
  border-style: solid;
  height: 16px;
  width: 16px;
}
::-webkit-scrollbar-button:single-button:vertical:decrement {
  border-width: 0 8px 8px 8px;
  border-color: transparent transparent white transparent;
}

::-webkit-scrollbar-button:single-button:vertical:increment {
  border-width: 8px 8px 0 8px;
  border-color: white transparent transparent transparent;
}
