#nothing-to-see-here {
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 5vw;
}
#wrapper {
  height: 100vh;
  width: 100vw;
}
#root {
  overflow: hidden;
}
.search-results-container {
  background: rgb(20, 20, 20);
  position: fixed;
  top: 65px;
  height: 100%;
  width: 100%;
  z-index: 25;
  overflow-y: auto;
}
.search-results {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 10px auto;
  width: 98%;
